"use client";

import { useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { useSearchParams } from "next/navigation";
import { useIframeSdk } from "@whop/react/iframe";
import {
  MdSavings,
  MdRateReview,
  MdSettings,
  MdKeyboardArrowDown,
  MdLogout,
  MdLightMode,
  MdDarkMode,
  MdSchedule,
  MdBarChart,
  MdAttachMoney,
  MdSwapHoriz,
  MdHelpOutline,
  MdBugReport,
  MdLightbulbOutline,
} from "react-icons/md";
import { FaDiscord } from "react-icons/fa";
import type { IconType } from "react-icons";
import { cn } from "@/lib/utils";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";

// Custom home icon component that switches between active/inactive states
function WalietHomeIcon({ active, className }: { active?: boolean; className?: string }) {
  return (
    <Image
      src={active ? "/icons/WalietHomeActive.png" : "/icons/WalietHomeInactive.png"}
      alt="Home"
      width={16}
      height={16}
      className={cn(
        className,
        active
          // Active: In light mode (solid gold bg), make icon white
          // Active: In dark mode (transparent gold bg), keep gold icon
          ? "brightness-0 invert dark:brightness-100 dark:invert-0"
          // Inactive: In dark mode on hover, make icon white
          : "dark:group-hover:brightness-0 dark:group-hover:invert"
      )}
    />
  );
}

// Placeholder icon type for custom icons
const HOME_ICON_PLACEHOLDER = "HOME" as const;

interface MenuItem {
  title: string;
  tab: string;
  icon: IconType | typeof HOME_ICON_PLACEHOLDER;
}

// Buyer (default) menu items
const buyerMenuItems: MenuItem[] = [
  { title: "Browse", tab: "browse", icon: HOME_ICON_PLACEHOLDER },
  { title: "Wallet", tab: "wallet", icon: MdSavings },
  { title: "Offers", tab: "offers", icon: MdRateReview },
  { title: "Settings", tab: "profile", icon: MdSettings },
];

// Seller menu items
const sellerMenuItems: MenuItem[] = [
  { title: "Dashboard", tab: "dashboard", icon: HOME_ICON_PLACEHOLDER },
  { title: "Requests", tab: "requests", icon: MdSchedule },
  { title: "Offers", tab: "offers", icon: MdRateReview },
  { title: "Earnings", tab: "earnings", icon: MdAttachMoney },
  { title: "Analytics", tab: "analytics", icon: MdBarChart },
];

interface TopBarProps {
  experienceId: string;
  user?: {
    id: string;
    name: string | null;
    avatar: string | null;
    sellerProfile?: { id: string } | null;
  } | null;
}

export function TopBar({ experienceId, user }: TopBarProps) {
  const searchParams = useSearchParams();
  const currentTab = searchParams.get("tab") || "browse";
  const mode = searchParams.get("mode") || "buyer";
  const isSeller = mode === "seller" && user?.sellerProfile;

  const menuItems = isSeller ? sellerMenuItems : buyerMenuItems;
  const basePath = `/experiences/${experienceId}`;

  const [userMenuOpen, setUserMenuOpen] = useState(false);
  const [isDarkMode, setIsDarkMode] = useState(true);
  const iframeSdk = useIframeSdk();

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);
    document.documentElement.classList.toggle("dark");
  };

  const handleSignOut = async () => {
    setUserMenuOpen(false);

    // Check if we're in a Whop iframe
    const isInIframe = window.self !== window.top;

    if (isInIframe) {
      // In iframe: close the app (returns to Whop)
      try {
        await iframeSdk.closeApp(null);
      } catch {
        // Fallback: redirect to Whop
        window.top?.location.replace("https://whop.com");
      }
    } else {
      // Standalone: redirect to home/login
      window.location.href = "/";
    }
  };

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="flex h-14 items-center justify-between px-4">
        {/* Spacer for centering */}
        <div className="hidden md:flex w-24" />

        {/* Navigation - Desktop (Centered) */}
        <nav className="hidden md:flex items-center justify-center gap-1 flex-1">
          {menuItems.map((item) => {
            const isActive = currentTab === item.tab;
            const isHomeIcon = item.icon === HOME_ICON_PLACEHOLDER;
            const Icon = item.icon as IconType;
            return (
              <Link
                key={item.tab}
                href={`${basePath}?tab=${item.tab}&mode=${mode}`}
                className={cn(
                  "group flex items-center gap-2 px-3 py-2 rounded-lg text-sm font-medium transition-colors",
                  isActive
                    ? "bg-primary text-primary-foreground dark:bg-primary/10 dark:text-primary"
                    : "text-muted-foreground hover:text-foreground hover:bg-muted"
                )}
              >
                {isHomeIcon ? (
                  <WalietHomeIcon active={isActive} className="h-4 w-4" />
                ) : (
                  <Icon className="h-4 w-4" />
                )}
                <span>{item.title}</span>
              </Link>
            );
          })}
        </nav>

        {/* Right side */}
        <div className="flex items-center justify-end gap-2 w-24 md:w-auto">
          {/* Mode Switcher */}
          <Link
            href={
              isSeller
                ? `${basePath}?tab=browse&mode=buyer`
                : `${basePath}?tab=dashboard&mode=seller`
            }
            className="flex items-center gap-2 px-3 py-1.5 rounded-lg text-xs font-medium bg-muted hover:bg-muted/80 transition-colors"
          >
            <MdSwapHoriz className="h-4 w-4" />
            <span className="hidden sm:inline">
              {isSeller ? "Buyer Mode" : "Seller Mode"}
            </span>
          </Link>

          {/* User Menu */}
          <Popover open={userMenuOpen} onOpenChange={setUserMenuOpen}>
            <PopoverTrigger asChild>
              <button className="flex items-center gap-2 rounded-lg p-1.5 hover:bg-muted transition-colors">
                <Avatar className="h-8 w-8">
                  <AvatarImage src={user?.avatar || undefined} />
                  <AvatarFallback className="bg-muted text-muted-foreground text-xs">
                    {user?.name?.[0]?.toUpperCase() || "?"}
                  </AvatarFallback>
                </Avatar>
                <MdKeyboardArrowDown className="h-4 w-4 text-muted-foreground hidden sm:block" />
              </button>
            </PopoverTrigger>
            <PopoverContent className="w-64 p-0 bg-background border border-border rounded-xl overflow-hidden" align="end" sideOffset={8}>
              <div className="p-3">
                {/* User Info + Theme Toggle */}
                <div className="flex items-center justify-between mb-3 px-2 py-2">
                  <div className="min-w-0">
                    <p className="text-sm font-medium text-foreground truncate">
                      {user?.name || "Anonymous"}
                    </p>
                    <p className="text-xs text-muted-foreground truncate max-w-[120px]">
                      {isSeller ? "Seller Mode" : "Buyer Mode"}
                    </p>
                  </div>
                  <div className="flex items-center gap-0.5 p-0.5 rounded-md bg-muted/50">
                    <button
                      onClick={() => {
                        setIsDarkMode(false);
                        document.documentElement.classList.remove("dark");
                      }}
                      className={`p-1.5 rounded-md transition-colors cursor-pointer ${
                        !isDarkMode
                          ? "bg-accent text-accent-foreground"
                          : "text-muted-foreground hover:text-foreground"
                      }`}
                    >
                      <MdLightMode className="h-4 w-4" />
                    </button>
                    <button
                      onClick={() => {
                        setIsDarkMode(true);
                        document.documentElement.classList.add("dark");
                      }}
                      className={`p-1.5 rounded-md transition-colors cursor-pointer ${
                        isDarkMode
                          ? "bg-accent text-accent-foreground"
                          : "text-muted-foreground hover:text-foreground"
                      }`}
                    >
                      <MdDarkMode className="h-4 w-4" />
                    </button>
                  </div>
                </div>

                {/* Menu Items */}
                <div className="space-y-0.5 mb-3">
                  <button
                    onClick={() => window.open("https://discord.gg/waliet", "_blank")}
                    className="w-full flex items-center gap-3 px-2.5 py-2 rounded-md text-muted-foreground hover:text-foreground hover:bg-muted/50 transition-colors cursor-pointer"
                  >
                    <FaDiscord className="h-4 w-4" />
                    <span className="text-sm font-medium">Discord</span>
                  </button>
                  <button
                    onClick={() => window.open("mailto:support@waliet.com", "_blank")}
                    className="w-full flex items-center gap-3 px-2.5 py-2 rounded-md text-muted-foreground hover:text-foreground hover:bg-muted/50 transition-colors cursor-pointer"
                  >
                    <MdHelpOutline className="h-4 w-4" />
                    <span className="text-sm font-medium">Support</span>
                  </button>
                  <button
                    onClick={() => window.open("mailto:feedback@waliet.com?subject=Feature%20Request", "_blank")}
                    className="w-full flex items-center gap-3 px-2.5 py-2 rounded-md text-muted-foreground hover:text-foreground hover:bg-muted/50 transition-colors cursor-pointer"
                  >
                    <MdLightbulbOutline className="h-4 w-4" />
                    <span className="text-sm font-medium">Feature Request</span>
                  </button>
                  <button
                    onClick={() => window.open("mailto:bugs@waliet.com?subject=Bug%20Report", "_blank")}
                    className="w-full flex items-center gap-3 px-2.5 py-2 rounded-md text-muted-foreground hover:text-foreground hover:bg-muted/50 transition-colors cursor-pointer"
                  >
                    <MdBugReport className="h-4 w-4" />
                    <span className="text-sm font-medium">Report Bug</span>
                  </button>
                </div>

                {/* Sign Out Button */}
                <button
                  onClick={handleSignOut}
                  className="w-full flex items-center justify-center gap-2 px-3 py-2 bg-muted hover:bg-muted/80 text-foreground rounded-lg transition-colors cursor-pointer"
                >
                  <MdLogout className="h-4 w-4" />
                  <span className="text-sm font-medium">Sign Out</span>
                </button>
              </div>
            </PopoverContent>
          </Popover>
        </div>
      </div>

    </header>
  );
}

// Mobile Bottom Navigation Bar
export function MobileBottomNav({ experienceId, currentTab, mode }: {
  experienceId: string;
  currentTab: string;
  mode: string;
}) {
  const isSeller = mode === "seller";
  const menuItems = isSeller ? sellerMenuItems : buyerMenuItems;
  const basePath = `/experiences/${experienceId}`;

  // Only show first 5 items to fit in bottom bar
  const visibleItems = menuItems.slice(0, 5);

  return (
    <nav className="md:hidden fixed bottom-0 left-0 right-0 z-50 bg-background border-t border-border pb-[env(safe-area-inset-bottom)]">
      <div className="flex items-center justify-around px-2 py-2">
        {visibleItems.map((item) => {
          const isActive = currentTab === item.tab;
          const isHomeIcon = item.icon === HOME_ICON_PLACEHOLDER;
          const Icon = item.icon as IconType;
          return (
            <Link
              key={item.tab}
              href={`${basePath}?tab=${item.tab}&mode=${mode}`}
              className={cn(
                "group flex flex-col items-center gap-1 px-3 py-2 rounded-xl min-w-[64px] transition-colors",
                isActive
                  ? "bg-primary text-primary-foreground dark:bg-primary/10 dark:text-primary"
                  : "text-muted-foreground"
              )}
            >
              {isHomeIcon ? (
                <WalietHomeIcon active={isActive} className="h-5 w-5" />
              ) : (
                <Icon className="h-5 w-5" />
              )}
              <span className="text-[10px] font-medium">{item.title}</span>
            </Link>
          );
        })}
      </div>
    </nav>
  );
}
